html,
body {
  height: 100%;
  font-family: 'Heebo', sans-serif;
}
.Container{
  width: 400px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: column;
  height: 100%;
}
.Header{
  width:100%;
  height: 50px;
  margin-bottom: 15px;
  box-shadow: 0px 2px 20px 2px rgba(0,0,0,0.16), 0px 2px 30px 0px rgba(0,0,0,0.12);
  color:#2657E1;
  text-align: center;
  line-height: 50px;
  display: flex;
  flex-direction: row;
}

.Content {
  flex: 1 0 auto;
}

.Date{
  font-size: 1.5em;
  width: 12%;
  padding-left:5px;
}

.Title {
  font-size: 1.5em;
  margin-right: auto;
  padding-left: -12%;
  width: 76%;
}

.Rooms-Today{
  display:flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
}
.Room {
  display: block;
  width: 28%;
  height: 180px;
  
  border-radius: 5px;
  margin:5px;
  box-shadow: 0px 2px 5px 0px rgba(0,0,0,0.16), 0px 2px 10px 0px rgba(0,0,0,0.12);
  text-align:center;
}
.Room-number{
  font-size: 20px;
  margin-top:10px;
}
.Room-status{
  height: 50%;
  
  margin-top:10px;
  margin-left: auto;
  margin-right: auto;
}
.Floor {
  display: block;
  width: 98%;
  height: 30px;
  text-align:center;
  border-radius: 5px;
  margin:5px;
  box-shadow: 0px 2px 5px 0px rgba(0,0,0,0.16), 0px 2px 10px 0px rgba(0,0,0,0.12);
}
.Floor-number {
  font-size: 20px;
}
.RoomContent{
  display: flex;
  flex-direction: column;
}

.Footer{
  display:flex;
  flex-direction: row;
  margin-top: 15px;
  width:100%;
  height: 50px;
  box-shadow: 0px 2px 20px 2px rgba(0,0,0,0.16), 0px 2px 30px 0px rgba(0,0,0,0.12);
  color:#2657E1;
  text-align: center;
  line-height: 50px;
  flex: 0 0 auto;
  position: fixed;
  bottom: 0px;
}
.btn{
  display: block;
  width: 33.3%;
  height: 50px;
  color:#2657E1;
  
  margin-top: auto;
  margin-bottom: auto;
  box-shadow: 0px 1px 5px 0px rgba(0,0,0,0.2), 0px 2px 2px 0px rgba(0,0,0,0.14), 0px 3px 1px -2px rgba(0,0,0,0.12);
  background-color: #e0e0e0;
}
.btn:hover {
  background-color: rgb(140, 140, 187);
  color:black;
  box-shadow: 0px 5px 11px 0px rgba(0,0,0,0.18), 0px 4px 15px 0px rgba(0,0,0,0.15)
}
.ChatIcon {
  height: 100%;

}
.IconComments {
  font-size: 30px;
}

