.GuestInfo{
    font-size: 1em;
    color: #2657E1;
    text-align: right;
  }
  .row{
    display: flex;
    flex-direction: row;
    justify-content:space-between;
  }
  .btnStart, .btnFinish, .btnStatus {
    width: 10vw;
    height: 10vw;
    margin: 10px;
    border-radius: 2px;
    box-shadow: 2px 5px 5px 0px rgba(0,0,0,0.16), 3px 6px 7px 0px rgba(0,0,0,0.14), 4px 7px 10px 0px rgba(0,0,0,0.12);
  }
.Room-status-in{
    width: 100%;
}
#tasksToRoom{
  width: 50%;
}
#timeStart, #timeFinish {
    width:50%;
    text-align: center;
}

#difTime{
    margin-left: auto;
    margin-right: auto;
    width: auto;
}